import React from "react";
import Avatar from "../component/avatar/avatar";
import { subtitles } from "../component/card/card";
import { Menu } from "../component/card/index";
import data from "../component/info/info";
import Info from "../component/info/index";
import ikon from "../component/socMed/socMed";
import SocMed from "../component/socMed/index";

const input = masuk => {
  return <SocMed URL={masuk.URL} imgURL={masuk.imgURL} />;
};

const entry = datas => {
  return <Info head={datas.head} info={datas.info} />;
};

const SideBar = () => {
  return (
    <div className="side-bar">
      <Avatar />
      <Menu head={subtitles[0].head} />
      <div>{data.map(entry)}</div>
      <Menu head={subtitles[1].head} />
      <div>{ikon.map(input)}</div>
    </div>
  );
};

export default SideBar;
