import React from "react";

const Internship = props => {
  return (
    <div className="content__internship">
      <div className="internship__data">
        <h2>{props.waktu}</h2>
        <h2>{props.kantor}</h2>
      </div>
      <p>{props.posisi}</p>
    </div>
  );
};

export default Internship;
