import React from "react";
import "./styles.css";

const Skills = props => {
  return (
    <div className={props.skillsStyle}>
      <div className={props.percentStyle}>
        <svg className={props.svgStyle}>
          <circle className={props.circleStyle} cx="70" cy="70" r="70"></circle>
          <circle className={props.circleStyle} cx="70" cy="70" r="70"></circle>
        </svg>
        <img className={props.imgStyle} src={props.imgURL} alt=""></img>
      </div>
      <div className={props.nomStyle}>
        <h2 className={props.nomHstyle}>{props.percent}</h2>
      </div>
    </div>
  );
};

export default Skills;
