import React from "react";
import subTitles from "../component/card/card";
import Kartu from "../component/card/index";
import datas from "../component/internship/internship";
import Internship from "../component/internship/index";
import Education from "../component/education/index";
import isi from "../component/skills/skills";
import Skills from "../component/skills/index";

const masuk = isinya => {
  return (
    <Skills
      className="content__skills"
      imgURL={isinya.imgURL}
      percent={isinya.percent}
      skillsStyle={isinya.skillsStyle}
      percentStyle={isinya.percentStyle}
      svgStyle={isinya.svgStyle}
      circleStyle={isinya.circleStyle}
      imgStyle={isinya.imgStyle}
      nomStyle={isinya.nomStyle}
      nomHstyle={isinya.nomHstyle}
    />
  );
};

const entry = data => {
  return (
    <Internship waktu={data.waktu} kantor={data.kantor} posisi={data.posisi} />
  );
};

const Middle = () => {
  return (
    <div className="content-middle">
      <Kartu img={subTitles[0].imgURL} head={subTitles[0].head} />
      <div>{datas.map(entry)}</div>
      <Kartu img={subTitles[1].imgURL} head={subTitles[1].head} />
      <Education />
      <Kartu img={subTitles[2].imgURL} head={subTitles[2].head} />
      <div className="content__skills">{isi.map(masuk)}</div>
    </div>
  );
};

export default Middle;
