import React from "react";

const Kartu = props => {
  return (
    <div className="content__subtitle">
      <div className="subtitle__image">
        <img class="subtitle__img" src={props.img} alt="education" />
      </div>
      <h2 className="subtitle__h2">{props.head}</h2>
    </div>
  );
};

const Menu = props => {
  return <h2 class="sidebar__subtitle">{props.head}</h2>;
};

export default Kartu;
export { Menu };
